import TitleForm from './TitleForm';

function Login() {
  return (
    <TitleForm title="Вход" submitText="Войти">
      <input className="form__input" type="email" placeholder="Email" name="email" required></input>
      <input
        className="form__input"
        type="password"
        placeholder="Пароль"
        name="password"
        required
      ></input>
    </TitleForm>
  );
}

export default Login;
